@import url(https://fonts.googleapis.com/css?family=Archivo&display=swap);
body {
  margin: 0;
  font-family: 'Archivo', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #EAE8E3;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.a-color-swatch{
  z-index: 100;
  color: #203342;
}

.am-wrapper.sm{
  text-align: left;
  width: 100%;
}

.am-wrapper.bg{
  text-align: center;
  color: #203342;
}

.am-logo.bg{
  margin-bottom:32px;
  margin-top: 64px;
  height: 128px;
  width: 128px;
}

.am-logo.sm{
  display: inline-block;
  margin-top: 16px;
  margin-left: 24px;
  height: 64px;
  width: 64px;
}

.am-tray-icon{
  color: #203342;
  transition: all .2s ease-in-out;;
}

.am-tray-icon.closed{
  display: inline-block;
}

.am-open-portfolio{
  display: inline-block;
  margin: auto;
  cursor: pointer;
}

.am-tray-icon.open{
  margin-top: 48px;
}

.am-open-portfolio:hover .am-tray-icon.open{
    transform: scale(1.2);
}

.am-open-portfolio:hover .am-op-text{
    text-decoration: underline;
    -webkit-text-decoration-color: #DD6E42;
            text-decoration-color: #DD6E42;
}

.am-tray-icon.open p{
  margin-bottom: 8px;
}

.am-info-header.sm{
  display: inline-block;
  margin-right: 8px;
}

.am-info{
  color: #203342;
}

.am-info-header{
  margin-top: 0px;
  margin-bottom: 0px;
}

.am-info:hover .am-info-header.sm{
  text-decoration: underline;
  -webkit-text-decoration-color: #DD6E42;
          text-decoration-color: #DD6E42;
}

.am-info.sm{
  display: inline-block;
  float: right;
  margin-top: 24px;
  margin-right: 24px;
  cursor: pointer;
}

.am-info.sm .am-icon{
  height: 24px;
}

.am-action-btns{
  margin-top: 48px;
}

.am-action-btn{
  display: inline-block;
  margin-left: 32px;
  margin-right: 32px;
  color: #203342;
  text-decoration: none;
}

.am-action-btn:hover{
  text-decoration: underline;
  -webkit-text-decoration-color: #DD6E42;
          text-decoration-color: #DD6E42;
}

.PortfolioList{
  margin: 24px;
  margin-top: 64px;
  color: #203342;
  text-align: center;
}

.cs-color-swatches{
  width: 64px;
  height: 22px;
  box-shadow: 0 1px 2px rgba(0,0,0,0.15);
  transition: all .2s ease-in-out;
}

.cs-btm-r{
  z-index: 100;
  position: fixed;
  bottom: 32px;
  right: 32px;
}

.cs-color-swatches:hover{
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
  transform: scale(1.2);
}

.cs-color-swatch{
  height: 100%;
  width: 33%;
  display: inline-block;
}

.cs-color-swatch.blue{
  background-color: #203342;
}

.cs-color-swatch.orange{
  background-color: #DD6E42;
}

.cs-color-swatch.off-white{
  background-color: #E8DAB2;
}

.PortfolioItem{
  text-align: left;
  max-width: 800px;
  margin: auto;
  margin-bottom: 96px;
  cursor: pointer;
}

.pi-image{
  height: 128px;
  width: 128px;
  margin-top: 8px;
  display: inline-block;
  vertical-align: top;
  border-radius: 16px;
  transition: all .2s ease-in-out;
}

.PortfolioItem:hover .pi-image{
  margin-top:20px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
  transform: scale(1.2);
}

.pi-info{
  display: inline-block;
  margin-left: 32px;
  max-width: 640px;
}

.pi-info-header{
  margin-top:0px;
  margin-bottom:0px;
}

.PortfolioItem:hover .pi-info-header{
  text-decoration: underline;
  -webkit-text-decoration-color: #DD6E42;
          text-decoration-color: #DD6E42;
}

